@use "../mixins";

.reviews {
  display: flex;
  flex-direction: column;

  padding: 30px;

  @media (min-width: 768px) {
    padding-left: 50px;
    padding-right: 50px;
    background-image: url(../../img/common/review.jpg);
    background-position-x: 0;
    background-repeat: no-repeat;
  }
}

.reviews__wrapper {
  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  @media (min-width: 1150px) {
    grid-template-columns: repeat(6, 1fr);
  }
}

.reviews__title {
  @include mixins.subtitle;

  margin-top: 0;
  margin-bottom: 15px;

  @media (min-width: 768px) {
    grid-column: 2/4;
  }
}

.reviews__list {
  @media (min-width: 768px) {
    grid-column: 1/5;

    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  @media (min-width: 1150px) {
    grid-column: 2/7;
    grid-template-columns: repeat(5, 1fr);
  }
}

.reviews__item {
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-bottom: 30px;

  @media (min-width: 768px) {
    grid-column: 1/6;
    grid-row: 1/3;
    margin-bottom: 0;
  }
}

.reviews__text {
  font-size: 15px;

  margin-top: 0;
  margin-bottom: 15px;
}

.reviews__author {
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  font-style: normal;
  color: var(--text-color);
}

.reviews__link {
  font-size: 17px;
  color: var(--text-body);
  text-decoration: none;
}

.reviews__controls {
  display: flex;
  margin-bottom: 40px;

  @media (min-width: 768px) {
    grid-column: 4/5;
    grid-row: 2/3;
    margin-bottom: 0;
  }

  @media (min-width: 1150px) {
    grid-column: 5/6;
  }
}

.reviews__control {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 52px;
  flex-grow: 1;
}

.reviews__button {
  @media (min-width: 768px) {
    grid-column: 4/5;
    grid-row: 1/2;
  }

  @media (min-width: 1150px) {
    grid-column: 6/7;
  }
}
